transform $base-duration $base-timing;\n      }\n    }\n  }\n}\n\n\n.category-panel {\n  flex: 1 1 60vh;\n  overflow-y: auto;\n\n  &--type {\n    background-color: $brand-green;\n    color: white;\n    padding: ms(-6) ms(2);\n\n  }\n\n  &--entry {\n    width: 100%;\n    display: flex;\n    flex-direction: row;\n    text-align: left;\n    padding: ms(2) ms(2) ms(2) ms(2)/2;\n    cursor: pointer;\n    border-left: ms(2)/2 solid transparent;\n\n    + .category-panel--entry {\n      border-top: 1px solid $light-gray;\n    }\n\n    &:hover {\n      background-color: $light-gray;\n\n      .category-panel--entry--location {\n        background-color: $medium-gray;\n      }\n    }\n\n    &--text {\n      overflow: hidden;\n      flex: 1;\n      margin-right: auto;\n    }\n\n    &--title {\n      text-transform: capitalize;\n      overflow: hidden;\n      text-overflow: ellipsis;\n      white-space: nowrap;\n    }\n\n    &--type {\n      font-size: ms(-2);\n      color: $gray;\n      text-transform: uppercase;\n      letter-spacing: 0.08em;\n      margin-top: -ms(0);\n      text-overflow: ellipsis;\n      white-space: nowrap;\n\n    }\n\n    &--location {\n      white-space: nowrap;\n      margin-top: auto;\n      font-size: ms(-1);\n      margin-left: ms(-1);\n      background-color: $light-gray;\n      border-radius: 1em;\n      padding: 0 ms(-2);\n      color: $gray;\n    }\n\n    &.selected {\n      border-top: none;\n      border-bottom: none;\n      border-left-color: $brand-green;\n      background-color: $brand-light-green;\n\n      .category-panel--entry--location {\n        background-color: $brand-green;\n      }\n\n\n    }\n  }\n}\n\n",".fade-enter-active, .fade-leave-active {\n  transition: opacity 0.5s;\n}\n.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {\n  opacity: 0;\n}\n\n.popup-enter-active, .popup-leave-active {\n  transition: transform 0.75s;\n  transition-timing-function: ease-in;\n}\n\n.popup-leave-active {\n  transition-timin